import React from 'react';
import s from './SearchFeatures.module.scss';
import searchSplash from 'images/HomePageImages/SearchSplash.jpg';
import searchResults from 'images/HomePageImages/SearchResults.png';
import { CustomButton } from 'components/CustomButton/CustomButton';
import arrowRight from 'images/arrowRight.svg';
import circleSvg from 'images/circle.svg';

const searchTypes = [
  {
    name: 'By company name',
    icon: circleSvg,
  },
  {
    name: 'By email',
    icon: circleSvg,
  },
  {
    name: 'By site name',
    icon: circleSvg,
  },
  {
    name: 'By city',
    icon: circleSvg,
  },
];

const SearchFeatures = () => {
  
  const searchTypeList = searchTypes.map((type, index) => {
    return (
      <div key={index} className={s.searchType}>
        <type.icon/>
        <div className={s.typeName}>
          { type.name }
        </div>
      </div>
    );
  });
  
  return (
    <div className={s.searchFeatureContainer}>
      <div className={s.searchImage}>
        <img
          draggable={false}
          src={searchSplash}
          alt={'background'}
          className={s.bgSearchImage}
        />
        <img
          draggable={false}
          src={searchResults}
          alt={'search results'}
          className={s.innerSearchImage}
        />
      </div>
      <div className={s.searchDescription}>
        <div className={s.descriptionContainer}>
          <div className={s.title}>
            <div className={s.primaryText}>
              Search
            </div>
            <div className={s.secondaryText}>
              other businesses
            </div>
          </div>
          <div className={s.list}>
            { searchTypeList }
          </div>
          <div className={s.searchButton}>
            <CustomButton
              variant='action'
              text={'Start searching'}
              image={arrowRight}
              imagePos='end'
              size={'l'}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default SearchFeatures;